.App {
  text-align: center;
  background-color: #345;
  overflow-x: hidden;
  max-width: 100%;
}

.skillscon{
  display: flex;
  flex-direction: row;
  background-color: #345;
  flex-wrap: nowrap;
  justify-content: center;
}

@media (max-width: 440px){
  .skillscon{
    display: flex;
    flex-direction: column;
    background-color: #264653;
    flex-wrap: nowrap;
    gap: 3em;
  }
}