.Title
    display: flex
    width: 100%
    height: 90vh
    text-align: center
    justify-content: center
    align-items: center
    background: #264653
    color: #fff


.titleContainer
    display: flex
    width: 60%
    align-items: center
    justify-content: center


@property --num 
  syntax: "<integer>"
  initial-value: 26*26*26*26*26*26+ 1*26*26*26*26+ 13*26*26*26+ 15*26*26+ 18*26 + 1
  inherits: false


.titleName 
  font-size: 4em
  transition: all 1s ease-out
  z-index: 1
  font-weight: lighter
  transition: --num 0.25s
  counter-set: num var(--num)
  padding: 2rem
  transform: translateY(-.5em)
  font-weight: lighter

.titleName::after 
  content: counter(num, upper-alpha)

.titleName:hover 
  --num: #{3*26 + 22}

