.Courses{
    width: 100%;
    background: #264653;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
}

.coursesContainer{
    display: flex;
    flex-direction: column;
    width: 60%;
    opacity: 0;
    transition: all .7s ease-out;
    transform: translateY(2em);
    margin-top: 1em;
    font-size: 1.1em;

}

.ofertedBy{
    color: #a8a8a8;
    font-weight: 200;
}

@media (max-width: 500px){
    .coursesContainer{
        display: flex;
        flex-direction: column;
        width: 70%;
        opacity: 0;
        transition: all .7s ease-out;
        transform: translateY(2em);
        margin-top: 1em;
        font-size: 1.1em;
    
    }
}