.Contact{
    width: 100%;
    height: auto;
    background-color: #0d1f22;
    color: #fff;
    display: flex;
    flex-direction: row;
    flex-grow: 1;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 10px #e9c46a;
}

.icon{
    font-size: 2em;
    padding: .5em;
    transition: all .2s ease-in;
    color: #fff;
}

.icon:hover{
    color: #e9c46a;
    text-shadow: 0px 0px 4px #e9c46a;
    box-shadow: 0px 0px 4px #e9c46a;

}
