.Footer{
    width: 100%;
    height: auto;
    background-color: #0d1f22;
}

.variousInfo{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.legalIcon{
    color: #fff;
    padding: .5em;
    padding-bottom: 1em;
    transition: all .2s ease-in;
}

.legalIcon:hover{
    color: #e9c46a;
}

.sign{
    text-align: center;
    color: #fff;
    background: linear-gradient(180deg, transparent, #264653);
    padding: 1em;
}