.Portfolio{
    width: 100%;
    color: #fff;
    display: flex;
    flex-direction: column;
    background-color: #264653;
    justify-content: center;
    align-items: center;
    overflow-x: hidden;
}

.PortfolioTitle{
    width: 80%;
    font-size: 1.2em;
    text-align: center;
    font-weight: 100;
}

.titlePContainer{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 60%;
    transition: all .7s ease-out;
    transform: translateY(10em);
    opacity: 0;
}

.arrows{
    font-size: 1.5em;
    color: #fff;
    display: flex;
    padding: 1em;
    font-weight: 100;
    z-index: 100;
}

.PortfolioContent{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    margin-bottom: 40vh;

}

.imagePContainer{
    width: 60%;
    height: auto;
    border: #fff 1px solid;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 1s ease-out;
    transform: translateY(5em);
    overflow: hidden;
}

.imagePContainer img{
    width: 100%;
    aspect-ratio: auto;
    transition: all 1s ease-out;

}

.points{
    padding: 0.2em;
    margin: 1em;
    border-radius: 50%;
    background-color: #fff;
}

@media (max-width: 500px){
    .titlePContainer{
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        width: 80%;
        transition: all .7s ease-out;
        transform: translateY(10em);
        opacity: 0;
    }

    .imagePContainer{
        width: 70%;
        height: auto;
        border: #fff 1px solid;
        border-radius: 5px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all 1s ease-out;
        transform: translateY(5em);
        overflow: hidden;
    }
}

@media (min-width: 1000px){
    .titlePContainer{
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        width: 40%;
        transition: all .7s ease-out;
        transform: translateY(10em);
        opacity: 0;
    }

    .imagePContainer{
        width: 40%;
        height: auto;
        border: #fff 1px solid;
        border-radius: 5px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all 1s ease-out;
        transform: translateY(5em);
        overflow: hidden;
    }
}