.Menu{
    z-index: 100;
    position: fixed;
    display: flex;
    flex-direction: column;
    width: 50%;
    border-bottom-right-radius:  100%;
    aspect-ratio: 1;
    transition: all 1s ease-out;
}

.backgroundMenu{
    z-index: 90;
    border-bottom-right-radius: 100%;
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    aspect-ratio: 1;
    transition: all .37s ease-out;
}

.menuContainer{
    z-index: 100;
    display: flex;
    visibility: hidden;
    flex-direction: column;
    margin: 0em 1em 1em 1em;
    color: #e9c46a;
    font-size: 1.2em;
    transition: all .5s ease-out;
}

.menu{
    z-index: 100;
    display: flex;
    width: 3em;
    aspect-ratio: 1;
    border: solid 1px #e9c46a;
    border-radius: 50%;
    text-align: center;
    justify-content: center;
    align-items: center;
    transition: all .2s ease-out;
    margin: 1em;
    color: #fff;
}

.Letter{
    font-size: 1.5em;
}


.menu:hover{
    width: 5em;
    background-color: #e9c46a;
    color: #345;
}


.menuItem{
    width: auto;
    padding: .25em;
    transition: all .2s ease-out;
    text-decoration: none;
    color: #fff;
    font-weight: 100;
}

.menuItem:hover{
    transform: translateX(.5em);
    text-shadow: 0px 0px 10px #fff;
}