.BriefIntro{
    width: 100%;
    background: #264653;
    color: #fff;
    align-items: flex-start;
    justify-content: center;
    display: flex;
    
}

.BriefIntroContainer{
    width: 60%;
    transition: all 1s ease-out;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;

}

.AboutMeTitle{
    width: 100%;
    overflow-x: hidden;
    height: auto;
    font-size: 1.8em;
    display: flex;
    margin-bottom: 1em;
    margin-top: 1em;
    text-align: center;
    justify-content: center;
    align-items: center;
    transition: all 1s ease-out;
    flex-shrink: 1;
    font-weight: 100;
    transform: translateY(3em);
    color: #f4a261;
}

.AboutMeParagraph{
    text-align: justify;
    text-justify: inter-word;
    max-width: 100%;
    transition: all 1s ease-out;
    margin-bottom: 2em;
    color: #dcdcdc;
    display: flex;
    flex-shrink: 1;
    font-weight: 200;
    font-size: 1.1em;
    transform: translateY(3em);
}


.AboutMeAnimation{
    animation: aboutEntering .5s;

}

@keyframes aboutEntering {
    0%{
        transform: translateY(3em);
    }
    100%{
        transform: translateY(0em);
    }
}


@media (max-width: 500px){
    .BriefIntroContainer{
        width: 80%;
        transition: all 1s ease-out;
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
    
    }
}