.CircleImage{
    display: flex;
    width: 100%;
    background: #264653;
    justify-content: center;
    align-items: center;
}

.circleContainer{
    width: 100%;
    aspect-ratio: 1;
    border: 1px solid #E9C46A;
    border-radius: 50%;
    background-image: url('./resources/profileimage/profilepic.jpg');
    background-size: cover;
    transition: all .2s ease-out;
    transform: translateY(-5em);
}

.secondCircle{
    width: 30%;
}

.circleContainer:hover{
    box-shadow: #fff 0 0 120px 20px;
}

.circleContainer:active{
    box-shadow: #F4A261 0 0 120px 20px;
}


@media (max-width: 800px){
    .secondCircle{
        width: 50%;
    }
}