.titles{
    font-size: 1.8em;
    font-weight: 100;
    color: #fff;
    background-color: #264653;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .titles div{
    width: 80%;
    margin-bottom: 1em;
    transform: translateX(-5em);
    transition: all 1s ease-out;
    color: #e9c46a;
  }