.TotalCourses{
    background-color: #264653;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.totalCoursesContainer{
    width: 60%;
    color: #fff;
    margin-top: 1em;
    font-size: 1.5em;
    font-weight: 100;
}

.totalCoursesBlock{
    text-align: left;
    opacity: 0;
    transition: all 1.8s ease-out;
}

.totalCoursesText{
    margin: .5em;
}