.Languages{
    background: #264653;
    width: 100%;
    display: flex;
    color: #fff;
    align-items: center;
    justify-content: center;
}

.langContainer{
    width: 40%;
    display: flex;
    flex-direction: row;
    opacity: 0;
    transition: all .7s ease-out;
    font-weight: 200;
    font-size: 1.1em;
    transform: translateY(3em);
}

.lang{
    text-align: left;
    padding: 1em;
}

.lev{
    text-align: left;
    padding: 1em;
    color: #a8a8a8; 
}