.SkillContainer{
    width: 100%;
    height: 2em;
    background-color: #264653;
    display: flex;
    color: #fff;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.Skill{
    width: 60%;
    align-items: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
}

.nameSkill{
    width: 20%;
    height: 1em;
    display: flex;
    text-align: right;
    justify-content: right;
    margin-right: 1em;
}

.progressSkill{
    width: 80%;
    height: 1.1em;
    display: flex;
    border: 1px solid #fff;
    border-radius: 5px;
    align-items: center;
}

.progressSkillbar{
    height: 100%;
    transition: all 1s ease-in;
    align-items: center;
    background-color: aliceblue;
    color: #000;
    transition: all .5s ease-out;
}